export function settingsSave(publish: boolean = false) {
	if (!Player.OnlineSettings)
		Player.OnlineSettings = <PlayerOnlineSettings>{};
    Player.OnlineSettings.LSCG = LZString.compressToBase64(JSON.stringify(Player.LSCG));
    window.ServerAccountUpdate.QueueData({OnlineSettings: Player.OnlineSettings});
	if (publish)
		getModule<CoreModule>("CoreModule")?.SendPublicPacket(false, "sync");
}

/** Checks if the `obj` is an object (not null, not array) */
export function isObject(obj: unknown): obj is Record<string, any> {
	return !!obj && typeof obj === "object" && !Array.isArray(obj);
}

export function escapeRegExp(string: string) {
	return string?.toLocaleLowerCase().replace(/[.*+?^${}()|[\]\\]/g, '\\$&') ?? ""; // $& means the whole matched string
}

export function isPhraseInString(string: string, phrase: string, ignoreOOC: boolean = false) {
	if (!string || string === "")
		return false;
	let praseMatch = new RegExp("\\b" + escapeRegExp(phrase) + "\\b", "i");
	let oocParsed = ignoreOOC ? string : excludeParentheticalContent(string);
	return praseMatch.test(oocParsed);
}

export function addCustomEffect(C: Character | null, effect: EffectName): boolean {
	if (!C)
		C = Player;
	let updated = false;
	const emoticon = C.Appearance.find((a) => a.Asset.Name === "Emoticon");
	if (!emoticon) {
		return updated;
	}

	if (Array.isArray(emoticon.Asset.AllowEffect))
		emoticon.Asset.AllowEffect.push(effect);
	else
		emoticon.Asset.AllowEffect = [effect];

	if (!emoticon.Property) {
		emoticon.Property = { Effect: [effect] };
		updated = true;
	} else if (!emoticon.Property.Effect) {
		emoticon.Property.Effect = [effect];
		updated = true;
	} else if (!emoticon.Property.Effect.includes(effect)) {
		emoticon.Property.Effect.push(effect);
		updated = true;
	}

	if (updated && ServerPlayerIsInChatRoom() && C == Player) {
		ChatRoomCharacterUpdate(Player);
		CharacterRelease(Player, true);
	}
	return updated;
}

export function removeCustomEffect(C: Character | null, effect: EffectName): boolean  {
	if (!C)
		C = Player;
	const emoticon = C.Appearance.find((a) => a.Asset.Name === "Emoticon");
	let updated = false;
	
	if (emoticon?.Property?.Effect?.includes(effect)) {
		emoticon.Property.Effect = emoticon.Property.Effect.filter(
			(e) => e !== effect
		);
		updated = true;
	}
	if (updated && ServerPlayerIsInChatRoom() && C == Player) {
		ChatRoomCharacterUpdate(Player);
	}
	return updated;
}

export function isAllowedMember(member: Character | undefined) {
	return !!member && ServerChatRoomGetAllowItem(member, Player);
}

export function drawSvg(
	ctx: CanvasRenderingContext2D,
	icon: string,
	x: number, y: number,
	width: number, height: number,
	baseSize: number,
	alpha: number,
	lineWidth: number,
	fillColor: string,
	strokeColor: string = "black"
) {
	ctx.save();
	ctx.globalAlpha = alpha;
	ctx.translate(x, y);
	ctx.scale(width / baseSize, height / baseSize);
	ctx.fillStyle = fillColor;
	if (strokeColor) {
		ctx.strokeStyle = strokeColor;
	}
	ctx.lineWidth = lineWidth;
	const p = new Path2D(icon);
	ctx.fill(p);
	if (strokeColor) {
		ctx.stroke(p);
	}
	ctx.restore();
}

